.toggleButton {
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    padding: 10px;
    background-color: #379683;
    width: 20%;
    text-align: center;
    color: #ffffff;
    font-family: Verdana;
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
    text-decoration: none;
}

.toggleButton:active {
    background-color: #b8b8b8;
    transform: translate(4px, 4px)
}


